<template>
    <div class="info-div" ref="trys" >
        <div v-if="showInfo" class="info-container">
            <info :row="row" @closeDiv="closeDiv" :controls="controls" ref="infoDetail" type="2"></info>
        </div>
        <div style="height: 100%;" v-if="!showInfo" class="StateMonitoring-info">
            <div class="list-content list-contentB detail-content">
                <div v-for="(item, index) in smokeDevice" class="box-top" :key="index" >
                    <svg height="80" width="80" >
                        <circle cx="40" cy="40" r="35" fill="none" stroke="#596781" stroke-width="5" stroke-linecap="round" class="fill"/>
                        <circle class="demo2" id="J_demo2" cx="40" cy="40" r="35" fill="none" :stroke="item.color" stroke-width="5" :stroke-dasharray="item.Percentage"/>
                    </svg>
                    <img :src="item.icon" class="icon">
                    <div class="smokeDevice" :style="{backgroundImage: 'url(' + item.bg + ')'}" >
                        <span style="margin-left: 17px">{{item.title}}</span>
                        <span style="margin-right: 10px" :style="{color: item.color}"><strong style="font-size: 1.5em">{{item.num}}</strong>个</span>
                    </div>
                    <!--<input id="J_btn_1" type="text" name="" value="10">-->
                    <!--<input id="J_btn_2" type="button" name="" value="set">-->
                </div>
            </div>
            <div class="list-content list-contentB" style="font-size: 0.94em;height: calc(85% - 2em);padding:0 1em 1em 1em; display: flex">
                <div class="box-table">
                    <div class="list-header noborder-ListHeader">
                        <i class="hasIcon hasImg">
                            <img src="dist/img/widsomApp/table_list.png" />
                        </i>
                        设备列表
                    </div>
                    <div class="operate-div">
                        <template v-for="(item, index) in formList">
                            <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom"></inputs>
                            <selects ref="selects"  v-if="item.type == 'select'" :option="item.options" @getSelect="getFrom"></selects>
                        </template>
                    </div>
                    <div ref="myOwnTable" class="table-container" style="height:calc(100% - 4.4em);border-bottom: 1px solid rgba(255, 255, 255, 0.15);">
                        <tables ref="myTable"  class="dialogTable" :tableOpt="dialogTable" style="margin:0em;width:100%;" @rowDbClick="MaxMapPoint"></tables>
                    </div>
                </div>
                <div class="smokeDataLog">
                    <div class="operate-div" style="margin-left: 1em">
                        <button class="button-operate" @click="fullScreen">
                            <i class="hasIcon hasImg">
                                <icon name="arrows-alt" scale="1.4"></icon>
                            </i>全屏
                        </button>
                    </div>
                    <div class="Monitoring-map" >
                        <div ref="MapBox" style="height: 100%;width: 100%">
                            <baiduMap ref="map" class="mapPanel" isInitMap="1" @initData="initMapData" v-if="maxMap" @MaxMapPoint="MaxMapPoint"></baiduMap>
                            <div class="min-mapBox" v-if="alarmData.length > 0">
                                <div class="map-title">
                                    <img src="dist/img/smokeFeeling/alarm_popup.png" alt="">
                                    <span><strong>{{alarmData.length}}</strong>条</span>
                                    <ul>
                                        <li v-for="(item, index) in alarmData" :class="{active: index===mapIndex}" @click="tabMap(index)"></li>
                                    </ul>
                                </div>
                                <p>设备编号: {{alarmData[mapIndex].cno}}</p>
                                <p>位置: {{alarmData[mapIndex].installAddr}}</p>
                                <p>更新时间: {{!!alarmData[mapIndex].updateTime ? getTrueDate(parseInt(alarmData[mapIndex].updateTime)):'--'}}</p>
                                <div class="min-map" v-if="minMap">
                                    <baiduMap ref="minMap" class="mapPanel"
                                              isInitMap="1"
                                              :otherObj="otherObj"
                                              @initData="initMinMapData"
                                              @minMapPoint="minMapPoint"
                                              mapID="minMapBox">
                                    </baiduMap>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <dialogs type="alert" ref="alerts"></dialogs>
        <dialogs type="customer" ref="detail" width="45em">
            <div slot="header">
                <span>{{title}}</span>
            </div>
            <div slot="content">
                <div class="staffMana-content" v-if="isDialogTable">
                    <tables ref="caution"  class="dialogTable" :tableOpt="deviceTable"></tables>
                </div>
            </div>
        </dialogs>
    </div>
</template>
<script>
    import 'vue-awesome/icons/arrows-alt'
    import info from './../info.vue'
    import baiduMap from './../../childComponent/baiduMap.vue'
    export default{
        data() {
            return {
                tableShow: false,
                mapIndex: 0,
                title: "",
                isDialogTable: false,
                showInfo: false,
                row: {},
                controls: [],
                dialogTable:{
                    column: [
                        {title: '序号', field: 'Index', textAlign: 'center',  width: 30},
                        {title: '状态', width: 40, field: 'status', textAlign: 'center',type: 'string',
                            formatter: function (val, index, row, obj) {
                            let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                            switch(vals) {
                                case '1':
                                    return `<div style="color: #2F7FC4">在线</div>`
                                case '2':
                                    return `<div style="color: #9FA1A0">离线</div>`;
                                case '3':
                                    return `<div>停用</div>`;
                                case '4':
                                    return `<div style="color: #F4564D">告警</div>`;
                                case '5':
                                    return `<div style="color: #E18137">欠压</div>`;
                                case '6':
                                    return `<div style="color: #3967D8">启用</div>`;
                            }

                        }},
                        {title: '设备编号', field: 'cno', textAlign: 'center',  width: 120},
                        {title: '安装位置', width: 140, field: 'installAddr', textAlign: 'center',type: "object",
                            formatter: function (val, index, row, obj) {
                                let template;
                                if(row.lat && row.lng){
                                    template = `<span class="list-operate"  @click="openAccount" style="color:#2F7FC4" title="${val}">${val}</span>`;
                                }else{
                                    template = `<span title="${val}">${val}</span>`;
                                }
                                return {
                                    template: template,
                                    methods: {
                                        openAccount: function () {
                                            obj.$parent.changeMap(row);
                                        }
                                    }
                                }

                            }},
                        {title: '更新时间', width: 100, field: 'updateTime', textAlign: 'center',type: 'string',
                            formatter: function (val, index, row, obj) {
                                let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                                if(vals == ''){
                                    return '';
                                }
                                return obj.getTrueDate(parseInt(vals))
                            }},
                    ],
                    data: [],
                    queryParam: {},
                    pageSize: 20,
                    pageNumber: 1,
                    height: 250,
                    noFooter: true,
                },
                minMap: false,
                maxMap: false,
                MapLoad: {
                    min: false,
                    max: false
                },
                DeviceTotal: {
                    total: 0,
                    online: 0,
                    offline: 0,
                    alarm: 0,
                    undervoltage: 0
                },
                searchParam: {
                    cno: "",
                    status: 0
                },
                deviceParam: {},
                tableData: "",
                alarmData: [],
                otherObj: {
                    Control: true,
                    centerAndZoom: 15
                },
            }
        },
        components: {
            baiduMap,
            info
        },
        computed: {
            commInfo: function(){
                return [
                    {text: '设备编号', value: this.detailData.cno || '--'},
                    {text: '通信编号', value: this.detailData.commNo || '--'},
                    {text: '负责人', value: this.detailData.chargeName || '--'},
                    {text: '负责人电话', value: this.detailData.chargeContact || '--'},
                    {text: '小区名称', value: this.detailData.resident || '--'},
                    {text: '安装位置', value: this.detailData.installAddr || '--'},
                    {text: '备注信息', value: this.detailData.remark || '--'},
                ]
            },
            formList: function () {
                return  [{
                    type: 'select',
                    options: {
                        width: '7em',
                        cs: 'client',
                        data: [
                            {text: '运行状态', value: 0},
                            {text: '在线', value: 1},
                            {text: '离线', value: 2},
                            {text: '停用', value: 3},
                            {text: '告警', value: 4},
                            {text: '欠压', value: 5},
                        ],
                        key: 'searchParam.status'
                    }
                },{
                    type: 'input',
                    options: {
                        type: 'typeSearch',
                        key: 'searchParam',
                        selectOpt: {
                            width: '7em',
                            cs: 'client',
                            data: [
                                {text: '设备编号', value: 'cno'}
                            ]
                        }
                    }
                }
                ]
            },
            smokeDevice: function () {
                console.log("---smokeDevice---")
                let DeviceTotal = this.DeviceTotal;
                let total = this.DeviceTotal.total;
                if(total == 0) {
                    total = 1;
                }
                return [
                    {color: "#3695E7", title: "设备总数量", bg: "dist/img/smokeFeeling/totle.png", num: this.DeviceTotal.total,icon: 'dist/img/smokeFeeling/totle_icon.png',Percentage:'220,1000'},
                    {color: "#EEB043", title: "在线数量", bg: "dist/img/smokeFeeling/oneline.png", num: DeviceTotal.online,icon: 'dist/img/smokeFeeling/online_icon.png',
                        Percentage: (DeviceTotal.online / total).toFixed(2) * 220 + ',1000'},
                    {color: "#A2A19F", title: "离线数量", bg: "dist/img/smokeFeeling/outline.png", num: DeviceTotal.offline,icon: 'dist/img/smokeFeeling/outline_icon.png',
                        Percentage: (DeviceTotal.offline / total).toFixed(2) * 220 + ',1000'},
                    {color: "#F25647", title: "告警数量", bg: "dist/img/smokeFeeling/alarm.png", num: DeviceTotal.alarm,icon: 'dist/img/smokeFeeling/alarm_icon.png',
                        Percentage: (DeviceTotal.alarm / total).toFixed(2) * 220 + ',1000'},
                    {color: "#E18137", title: "欠压数量", bg: "dist/img/smokeFeeling/low.png", num: DeviceTotal.undervoltage,icon: 'dist/img/smokeFeeling/low_icon.png',
                        Percentage: (DeviceTotal.undervoltage / total).toFixed(2) * 220 + ',1000'},
                ];
            }
        },
        watch: {
            tableData: {
                deep: true,
                handler: function (newVal,oldVal){
                    if(this.maxMap && this.MapLoad.max){
                        console.log('------max-----')
                        this.initMapData();
                        this.setAlarm();
                    }
                }
            },
            alarmData: {
                deep: true,
                handler: function (newVal,oldVal){
                    if(this.minMap && this.MapLoad.min){
                        console.log('-----min----')
                        this.initMinMapData();
                    }
                }
            }
        },
        methods: {
            deviceUpdate: function () {
                let self = this;
                self.getWebsocket({
                    keyMsg: 'autoUploadMsg',
                    sign: true,
                    funMsg: function (data) {
                        let datas = data.data;
                        self.compareData(datas)
                    }
                });
            },
            compareData: function (data) {
                if(data.code == 1){
                    let row = {};
                    let params = JSON.parse(data.dataSrc.params);
                    row.updateTime = new Date(data.dataSrc.dataTime).getTime();
                    for(let i = 0; i < params.length; i++){
                        if(params[i].readType == 17){
                            row.status = params[i].readValue;
                            break;
                        }
                    }
                    for (let i = 0; i < this.tableData.length; i++){
                        if(this.tableData[i].cno == data.dataSrc.cno){
                            if(this.tableData[i].status != row.status){
                                console.log('status')
                                this.tableData[i].status = row.status;
                            }
                            this.$refs.myTable.updateRow(i, row);
                            break;
                        }
                    }
                    for (let i in data.dataSrc.statusTotal){
                        console.log('--statusTotal----')
                        this.DeviceTotal[i] = data.dataSrc.statusTotal[i];
                    }
                }
            },
            closeDiv: function () {
                this.showInfo = false;
                this.deviceUpdate();
            },
            fullScreen: function () {
                var requestFullScreen=function(element) {
                    //某个元素有请求
                    var requestMethod =element.requestFullScreen
                        ||element.webkitRequestFullScreen //谷歌
                        ||element.mozRequestFullScreen  //火狐
                        ||element.msRequestFullScreen; //IE11
                    if (requestMethod) {
                        requestMethod.call(element);   //执行这个请求的方法
                    } else if (typeof window.ActiveXObject !== "undefined") {  //window.ActiveXObject判断是否支持ActiveX控件
                        //这里其实就是模拟了按下键盘的F11，使浏览器全屏
                        var wscript = new ActiveXObject("WScript.Shell"); //创建ActiveX
                        if (wscript !== null) {    //创建成功
                            wscript.SendKeys("{F11}");//触发f11
                        }
                    }
                }
                var elem = this.$refs.MapBox;
                requestFullScreen(elem);
            },
            MaxMapPoint: function (point) {
                let cno = point.cno || point.val.cno;
                let status = point.status || point.val.status;
                this.row.cno = cno;
                this.row.status = status;
                this.showInfo = true;
            },
            changeMap: function (row) {
                this.$refs.map.setCenterPoint([row], 19);
            },
            minMapPoint: function (point) {
                this.$refs.map.setCenterPoint(point, 19);
            },
            tabMap: function (index) {
                if(index === this.mapIndex) return;
                this.mapIndex = index;
                this.initMinMapData();
            },
            setAlarm: function () {
                this.alarmData = [];
                for(let i = 0; i < this.tableData.length; i++) {
                    if(this.tableData[i].status == 4){
                        if(this.alarmData.length === 5){
                            return;
                        }
                        this.alarmData.push(this.tableData[i]);
                    }
                }
            },
            windowChange: function () {
                this.getHeight();
            },
            getSmokeDeviceTotal: function () {
                let self = this;
                this.getRequest({
                    url: 'smoke/smokeDeviceTotal',
                    method: 'post',
                    param: {},
                    success: function (data) {
                        for (let i in data.data){
                            self.DeviceTotal[i] = data.data[i];
                        }
                    },
                    error: function (data) {
                        self.$refs.alerts.show({
                            msg: data.message
                        });
                    }
                }, true)
            },
            initMapData: function () {
                this.minMap = true;
                this.MapLoad.max = true;
                this.$refs.map.setCenterPoint(this.tableData);
            },
            initMinMapData: function () {
                this.MapLoad.min = true;
                this.$refs.minMap.setCenterPoint([this.alarmData[this.mapIndex]]);
            },
            getFrom: function (val) {
                if (val.combine) {
                    if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
                        this.getOption(val, true);
                        this.getDeviceDetect(1);
                    }
                } else {
                    if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
                        this.getOption(val, true);
                        this.getDeviceDetect();
                    }
                }
            },
            getDeviceDetect: function () {
                let self = this;
                this.MapLoad.min = false;
                this.getRequest({
                    url: 'smoke/smokeDeviceDetect',
                    method: 'post',
                    param: self.searchParam,
                    success: function (data) {
                        self.tableData = data.data;
                        for (let i = 0; i < self.tableData.length; i++){
                            self.tableData[i].index = i + 1;
                        }
                        self.setAlarm();
                        self.$refs.myTable.resetTableOpt({data: data.data});
                        self.maxMap = true;
                    },
                    error: function (data) {
                        self.$refs.alerts.show({
                            msg: data.message
                        });
                    }
                }, true)
            },
            setCircle: function () {
                var demo2 =  document.querySelector("#J_demo2");
                var btn1 = document.querySelector("#J_btn_1");
                var btn2 = document.querySelector("#J_btn_2");

                var circleLength = Math.floor(2 * Math.PI * demo2.getAttribute("r"));

                window.onload = rotateCircle;

                btn2.onclick = rotateCircle;
                function rotateCircle () {
                    var val = parseFloat(btn1.value).toFixed(2);
                    val = Math.max(0,val);
                    val = Math.min(100,val);
                    console.log(circleLength)
                    console.log(circleLength * val / 100)
                    demo2.setAttribute("stroke-dasharray","" + circleLength * val / 100 + ",10000");
                }
            },
            init: function () {
                this.controls[1] = true;
                this.controls[2] = true;
                this.controls[3] = true;
                this.controls[6] = true;
                this.getSmokeDeviceTotal();
                this.getDeviceDetect();
                this.deviceUpdate();
                this.tableShow = true;
            },
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            }
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                if(vm.showInfo) {
                    let obj = vm.$refs.infoDetail;
                    obj.init();
                } else {
                    vm.init();
                }
            });
        },
        beforeDestroy(){
            this.$store.state.callback.autoUploadMsg = null;
        },
        mounted: function () {
            this.isIn();
            this.getAction();
            this.windowChange();
        }
    }
</script>
<style lang="scss">
    $headerColor:#44BBDD;
    $leftContentW: 16%;
    $leftContentH: 1em;
    .StateMonitoring-info{
        .info-container{
            margin-top:5px
        }
        .smokeDataLog{
            flex: 6;
            height: 100%;
        }
        .box-table{
            height: 100%;
            flex: 4;
        }
        .Monitoring-map{
            margin-top: 4em;
            height: calc(100% - 4em);
            width: calc(100% - 1em);
            margin-left: 1em;
            clear: both;
            position: relative;
            .min-mapBox{
                position: absolute;
                bottom: 1px;
                right: 1px;
                border:2px solid #0F508A;
                background: #0F508A;
                height: 13.8em;
                width: 15.2em;
                text-align: left;
                border-radius: 5px;
                overflow: hidden;
                .map-title{
                    margin: 0 2px;
                    border-bottom: 1px solid #8EADC9;
                    position: relative;
                    height: 30px;
                    img{
                        position: absolute;
                        left: .5em;
                        top: .5em;
                    }
                    span{
                        position: absolute;
                        left: 30px;
                        top: 4px;
                        bottom: 3px;
                        overflow: hidden;
                        strong{
                            font-size: 24px;
                            margin-right: 3px;
                            font-weight: normal;
                        }
                    }
                    ul{
                        float: right;
                        height: 2em;
                        display: flex;
                        align-items: center;
                        .active{
                            background: #fff;
                        }
                        li{
                            float: left;
                            cursor: pointer;
                            width: 7px;
                            height: 7px;
                            border: 2px solid #fff;
                            border-radius: 50%;
                            margin-left: 5px;
                        }
                    }
                }
                p{
                    font-size: .8em;
                    margin: 5px 0;
                    text-indent: 2px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
                .min-map{
                    height: calc(100% - 4.2em - 28px);
                    width: 100%;
                }
            }
        }
        .demo2{
            transform-origin: 40px 40px;
            transform: rotate(-90deg);
            transition: stroke-dasharray .3s ease-in;
        }
        .detail-content{
            height:calc(15% - 1em);
            padding:0;
            display: flex;
            align-items: center;
            justify-content: space-around;
            min-height: 5em;
            .box-top{
                align-items: center;
                display: flex;
                position: relative;
                width: 16%;
                margin-right: 5%;
            }
            .box-top:last-child{
                margin: 0;
            }
            svg{
                height: 80px;
                width: 80px;
                position: relative;
                z-index: 1;
                .fill{
                    fill: #16335E;
                }
            }
            .icon{
                position: absolute;
                top: 25px;
                left: 25px;
                z-index: 2;
            }
            .smokeDevice{
                background-repeat: no-repeat;
                position: absolute;
                left: 64px;
                overflow: hidden;
                width: 12.5em;
                height: 56px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                background-size: 100% 100%;
            }
        }
    }
</style>



